*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body{
  color: white;
  background-color: #0F4C75;
  font-family: Lato, sans-serif;
  margin-top: 40px;
}
h1{
  color: #1b1b32;
  font-size: 25px;
  font-family: Lato, sans-serif;
  text-align: center;
  margin: 20px 0;
}

.app-task{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.logo{
  width: 400px;
  height: 50px;
}

/*List of task*/

.list-task-main{
  width: 600px;
  min-height: 500px;
  background-color: #d0d0d5;
  padding: 25px;
  margin: 10px;
  border-radius: 20px;
}
